export default {
    init: function (context) {
        let method = context.$store.getters.getMethod;
        if (method === 'addTask') {
            return {
                title: 'Новая задача',
                description: 'Описание задачи',
                singleLineInput: false,
                multiLineInput: true,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.addTask
            }
        }
        if (method === 'deleteColumn') {
            return {
                title: 'Удаление колонки',
                description: 'Вы действительно хотите удалить данную колонку?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteColumn
            }
        }
        if (method === 'addGroup') {
            return {
                title: 'Добавление группы задач',
                description: 'Название группы задач:',
                singleLineInput: true,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.addGroup
            }
        }
        if (method === 'deleteGroup') {
            return {
                title: 'Удаление группы задач',
                description: 'Вы действительно хотите удалить данную группу задач?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteGroup
            }
        }
        if (method === 'ungroup') {
            return {
                title: 'Разгруппирование',
                description: 'Вы действитедьно хотите разгруппировать данную группу задач?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.ungroup
            }
        }
        if (method === 'changeTitle') {
            return {
                title: 'Изменение заголовка',
                description: 'Новое название заголовка:',
                singleLineInput: true,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.changeTitle
            }
        }
        if (method === 'editTask') {
            return {
                title: 'Изменение задачи',
                description: 'Новый текст задачи:',
                singleLineInput: false,
                multiLineInput: true,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.editTask
            }
        }
        if (method === 'deleteTask') {
            return {
                title: 'Удаление задачи',
                description: 'Вы действительно хотите удалить данную задачу?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteTask
            }
        }
        if (method === 'editColumn') {
            return {
                title: 'Редактирование колонки',
                description: 'Название колонки:',
                singleLineInput: true,
                multiLineInput: false,
                toggle: true,
                cancel: context.closeDialogPanel,
                ok: context.editColumn
            }
        }
    }
}
